
body {
    color: #ffffff;
    background-color: #97baa5;
    font-size: 15px;
    font-family: 'Oswald'!important;
}

.row {
    margin-left: 0px;
    margin-right: 0px;
}

.padding0 {
    padding: 0px!important;
}

.textAlignCenter {
    text-align: center!important;
}

.displayFlex {
    display: flex;
}

.footer {
    padding: 20px 0px;
}


.hrLine {
    border-top: 2px solid white;
    width: 50%;
}

.socialSites {
    display: block;
    text-align: right;

    a {
        padding: 0px 8px;
    }
}

.copyrightText {
    display: block;
    text-align: right;
    font-size: 10px;
}

.displaySection {
    width: 85%;
    margin: 0px auto;
}

.navbar-light .navbar-nav .nav-link {
    color: #000!important;
    
    &:hover {
        background-color: #7eac91;
        border-radius: 8px;
    }
}
.navbar {
    padding: 15px 10px;
}

.invitationSection {
    background-color: #7eac91;

    .invitationSubSection {
        margin: 25px;
        padding: 15px;
        border: 10px solid #97baa5;
        width: auto;
    }
}

.subSection {
    margin: 10px 0px;
    padding: 0;
}

ul {
    // list-style: none;
    padding-left: 22px;
}

a {
    text-decoration: none!important;
    color: inherit;

    &:hover {
        color: inherit;
    }
}


.section {
    padding: 10px;

    .subSection {
        margin: 10px 0px;
        padding: 0;

        .titleSectionDiv {
            margin: auto;

            .subHeading {
                font-size: 18px;
                word-break: break-word;
            }

            .mainHeading {
                letter-spacing: 15px;
                word-break: break-word;
                word-spacing: 10px;
                line-height: 1.5;
                margin-bottom: 0px;
            }
        }
    }

    .titleSection {
        display: flex;
        vertical-align: middle;
        font-weight: 600;
    }

    .subHeading {
        text-transform: capitalize;
        font-size: 34px;
        word-break: break-word;
    }

    .subText {
        font-size: 20px;
        margin-bottom: 0px;
        text-align: justify;
    }

    .imageContainer {
        padding: 0px 15px;
        text-align: center;

        .mainImg {
            width: 100%;
            max-height: 480px;
        }

        .multipleImages {
            width: 100%;
            height: 165px;
        }

        .imgWithText {
            width: 55%;
        }
    }
}

.textSection {
    display: flex;
    vertical-align: middle;
}


.textSectionDiv {
    margin: auto 0px;
    padding: 0px 15px;
}

